import React, { Component } from 'react'
import styled from 'styled-components'

const StyledButton = styled.button`
  display: inline-flex;
  flex-flow: row;
  border-radius: 35px;
  margin: 20px 10px;
  padding: 4px 10px;
  transition: 0.3s;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  &:focus {
    outline: none;
    transform: scale(1.2);
  }
  &:active {
    outline: none;
    transform: scale(1.2);
  }
  .picked {
    border: 3px solid black;
  }
`

const possibleColors = [
  'Blue',
  'Green',
  'Yellow',
  'Black',
  'Orange',
  'Purple',
  'Red',
]

const RadioComponent = props => (
  <div>
    {possibleColors.map((color, index) => {
      return (
        <StyledButton
          style={{
            border: `3px solid ${possibleColors[index]}`,
          }}
          type="button"
          name="color"
          value={color}
          key={index}
          onClick={props.selectColor}
          className={
            possibleColors[index] === props.selectedColor ? 'picked' : ''
          }
        >
          Select
        </StyledButton>
      )
    })}
  </div>
)

export default RadioComponent
